:root {
  font-size: 14px;
}

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: hsl(228, 12%, 44%);
  border-color: hsl(228, 12%, 44%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
